/* lato-regular - latin */
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/lato-v20-latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("./fonts/lato-v20-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("./fonts/lato-v20-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("./fonts/lato-v20-latin-regular.woff")
      format("woff"),
    /* Modern Browsers */ url("./fonts/lato-v20-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */ url("./fonts/lato-v20-latin-regular.svg#Lato")
      format("svg"); /* Legacy iOS */
}
/* lato-700 - latin */
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  src: url("./fonts/lato-v20-latin-700.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("./fonts/lato-v20-latin-700.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("./fonts/lato-v20-latin-700.woff2") format("woff2"),
    /* Super Modern Browsers */ url("./fonts/lato-v20-latin-700.woff")
      format("woff"),
    /* Modern Browsers */ url("./fonts/lato-v20-latin-700.ttf")
      format("truetype"),
    /* Safari, Android, iOS */ url("./fonts/lato-v20-latin-700.svg#Lato")
      format("svg"); /* Legacy iOS */
}
.app-wrapper {
  display: grid;
  margin: 0 auto;
  width: 98%;
  grid-gap: 2px;
  grid-template-areas:
    "header"
    "nav"
    "content"
    "footer";
  font-family: "Lato";
  font-weight: 400;
}
.app-wrapper-content {
  grid-area: content;
  background-color: rgb(225, 183, 94);
}

@media (min-width: 576px) {
  .app-wrapper {
    width: 80%;
    grid-gap: 10px;
    grid-template-columns: 1fr 4fr;
    grid-template-areas:
      "header header header header header "
      "nav content content content content"
      "footer footer footer footer footer";
  }
}
